@keyframes animate1 {
  0% {
    left: -100%;
  }
  50%, 100% {
    left: 100%;
  }
}
@keyframes animate2 {
  0% {
    top: -100%;
  }
  50%, 100% {
    top: 100%;
  }
}
@keyframes animate3 {
  0% {
    right: -100%;
  }
  50%, 100% {
    right: 100%;
  }
}
@keyframes animate4 {
  0% {
    bottom: -100%;
  }
  50%, 100% {
    bottom: 100%;
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
}

html {
  height: 100vh;
}

body {
  margin: 0;
  height: auto;
  font-size: 16px;
  font-family: "Acme", "Microsoft JhengHei";
  /* Background pattern from Toptal Subtle Patterns */
  background-image: url("../img/bg.png");
  position: relative;
}

canvas {
  display: block;
  max-width: 100%;
}

textarea {
  resize: none;
  width: 100%;
  border: 1px solid #d8d8d8;
  border-radius: 5px;
}
textarea:focus {
  box-shadow: 0px 0px 5px 1px #5abee6;
  outline: 0;
}

label {
  display: inline-block;
  margin: 0.5rem 0;
  user-select: none;
}

input[type=range] {
  width: 100%;
  height: 30px;
  background-color: transparent;
  -webkit-appearance: none;
  cursor: pointer;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  background: #5abee6;
  height: 10px;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  background: #ffffff;
  width: 16px;
  height: 36px;
  border: 1px solid #000000;
  border-radius: 3px;
  margin-top: -13px;
}
input[type=range]::-moz-range-track {
  background: #5abee6;
  height: 10px;
}
input[type=range]::-moz-range-thumb {
  width: 16px;
  height: 36px;
  background: #ffffff;
  border: 1px solid #000000;
  border-radius: 3px;
  cursor: pointer;
}

input[type=color] {
  background-color: #d8d8d8;
  border: 0;
  border-radius: 5px;
  cursor: pointer;
}
input[type=color]:focus {
  box-shadow: 0px 0px 5px 1px #5abee6;
  outline: 0;
}

input[type=checkbox] {
  position: absolute;
  z-index: -1000;
  left: -1000px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
input[type=checkbox] + label {
  background-image: url("../img/checkbox.png");
  user-select: none;
  padding-right: 45px;
  height: 24px;
  width: 190px;
  display: inline-block;
  line-height: 24px;
  background-repeat: no-repeat;
  background-position: 150px 0;
  font-size: 24px;
  vertical-align: middle;
  cursor: pointer;
}
input[type=checkbox]:checked + label {
  background-position: 150px -24px;
}

select {
  border-radius: 10px;
  cursor: pointer;
  outline: none;
  padding: 4px 35px 4px 15px;
  border: 1px solid #5abee6;
  background-color: #fff;
  -moz-appearance: none;
  -webkit-appearance: none;
}

.layout_input {
  display: flex;
  align-items: center;
}
.layout_input label {
  width: 150px;
}

.hack_select {
  position: relative;
  display: flex;
  align-items: center;
}
.hack_select::after {
  content: "";
  position: absolute;
  top: 2px;
  width: 0;
  height: 0;
  right: 10px;
  bottom: 0;
  margin: auto;
  border-style: solid;
  border-width: 5px 5px 0 5px;
  border-color: #5abee6 transparent transparent transparent;
  pointer-events: none;
}

button {
  outline: none;
}
button.black {
  border: none;
  box-shadow: 0px 10px 14px -7px #000000;
  background: linear-gradient(to bottom, #000000 5%, #525252 100%);
  border-radius: 8px;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-size: 16px;
  font-weight: bold;
  padding: 4px;
  width: 120px;
  text-decoration: none;
  text-shadow: 0px -1px 0px #2f6627;
}
button.black:hover {
  background: linear-gradient(to bottom, #525252 5%, #000000 100%);
}
button.black:active {
  position: relative;
  top: 1px;
}
button.white {
  border: none;
  box-shadow: 0px 10px 14px -7px #000000;
  background: linear-gradient(to bottom, #ffffff 5%, #d9d6d9 100%);
  border-radius: 8px;
  display: inline-block;
  cursor: pointer;
  color: #000000;
  font-family: Arial;
  font-size: 16px;
  font-weight: bold;
  padding: 4px;
  width: 120px;
  text-decoration: none;
  text-shadow: 0px -1px 0px #2f6627;
}
button.white:hover {
  background: linear-gradient(to bottom, #d9d6d9 5%, #ffffff 100%);
}
button.white:active {
  position: relative;
  top: 1px;
}
button.transparent {
  border: none;
  box-shadow: 0px 10px 14px -7px #000000;
  background-color: transparent;
  border-radius: 8px;
  display: inline-block;
  cursor: pointer;
  color: #000000;
  font-family: Arial;
  font-size: 16px;
  font-weight: bold;
  padding: 4px;
  width: 120px;
  text-decoration: none;
  text-shadow: 0px -1px 0px #2f6627;
}
button.transparent:hover {
  box-shadow: 0px 10px 14px -5px #000000;
}
button.transparent:active {
  position: relative;
  top: 1px;
}

