.div_img-preview {
  height: auto;
  min-height: 300px;
  min-width: 500px;
  display: flex;
  margin: 0 auto;
  position: relative;
  background-color: #fff;
  border: 5px dashed #d43939;
  box-shadow: 5px 5px 10px #000;
  border-radius: 10px;
}
@media (max-width: 620px) {
  .div_img-preview {
    width: 100%;
    min-width: auto;
  }
}
.div_img-preview .loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  display: none;
  color: #ff7300;
}
.div_img-preview .loading.active {
  display: block;
}
.div_img-preview .hack_btn-upload {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  user-select: none;
}
.div_img-preview .hack_btn-upload .input_file {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}
.div_img-preview .hack_btn-upload .input_file + label * {
  pointer-events: none;
}
.div_img-preview .hack_btn-upload .label_input-file {
  display: block;
  margin-top: 0;
  text-align: center;
  padding: 0.8rem 0;
  width: 200px;
  position: relative;
  font-size: 1.25rem;
  font-weight: 700;
  color: #d43939;
  overflow: hidden;
  cursor: pointer;
  background-color: #f8e1e1;
}
.div_img-preview .hack_btn-upload .label_input-file:hover {
  background-color: #d43939;
  color: #050801;
  box-shadow: 0 0 5px #d43939, 0 0 25px #d43939, 0 0 50px #d43939, 0 0 200px #d43939;
}
.div_img-preview .hack_btn-upload .label_input-file span {
  position: absolute;
  display: block;
}
.div_img-preview .hack_btn-upload .label_input-file span:nth-child(1) {
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #d43939);
  animation: animate1 1s linear infinite;
}
.div_img-preview .hack_btn-upload .label_input-file span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #d43939);
  animation: animate2 1s linear infinite;
  animation-delay: 0.25s;
}
.div_img-preview .hack_btn-upload .label_input-file span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #d43939);
  animation: animate3 1s linear infinite;
  animation-delay: 0.5s;
}
.div_img-preview .hack_btn-upload .label_input-file span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(0deg, transparent, #d43939);
  animation: animate4 1s linear infinite;
  animation-delay: 0.75s;
}

