.modal-bg {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
.modal-bg.show {
  opacity: 1;
  visibility: visible;
}

.modal {
  position: relative;
  background-color: #fff;
  width: 500px;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 5px;
  background-color: #e68d8d;
}
@media (max-width: 620px) {
  .modal {
    width: 90%;
  }
}
.modal .modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-weight: bold;
  cursor: pointer;
}
.modal .modal-content {
  margin: 1rem;
  font-size: 1.5rem;
  color: #fff;
  text-align: center;
}
.modal .btn_ok {
  margin: 0;
  text-align: center;
  padding: 0.5rem;
  width: 100px;
  position: relative;
  font-size: 1.25em;
  font-weight: 700;
  color: #000000;
  background-color: transparent;
  border: 0;
  font-family: inherit;
  color: #2b9db9;
  overflow: hidden;
  cursor: pointer;
  background-color: #c0e7f0;
}
.modal .btn_ok:hover {
  background-color: #2b9db9;
  color: #050801;
  box-shadow: 0 0 5px #2b9db9, 0 0 25px #2b9db9, 0 0 50px #2b9db9, 0 0 200px #2b9db9;
}
.modal .btn_ok span {
  position: absolute;
