.preload {
  position: absolute;
  visibility: hidden;
}
.preload .acme {
  font-family: "Acme";
}
.preload .bangers {
  font-family: "Bangers";
}
.preload .roboto {
  font-family: "Roboto";
}

.btn_top {
  display: block;
  position: fixed;
  padding: 7px 8px;
  width: 30px;
  height: 30px;
  right: 20px;
  bottom: 20px;
  border-radius: 50%;
  background-color: #d43939;
  color: #ffffff;
  cursor: pointer;
  z-index: 5;
  opacity: 0;
  transform: translateY(100px);
  transition: all 0.5s ease;
}
.btn_top.show {
  opacity: 1;
  transform: translateY(0);
}